<template>
	<view class="HospitalGuide">
		<Cube
		 v-for="item in menuLists"
		 :key="item.id"
		 :icon="item.icon"
		 :content="item.content.name"
		 :index="item.content.index"
		 @gotoTargePage="gotoTargePage"
		 />
	</view>
</template>

<script>
import Cube from '@/components/buttons/cube.vue'
export default {
	name: 'HospitalGuide',
	components: {
		Cube
	},
	data() {
		return {
			// 15个icon
			icons: [
				'门诊指南',
				'急诊指南',
				'住院指南',
				'挂号预约',
				'诊室分布',
				'医保服务',
				'交通指南',
				'泊车指南',
				'生活指南',
				'医院介绍',
				'患者飞鸿',
				'医院荣誉',
				'健康文章',
				'健康视频'
			],
			menuLists: [],
			content: []
		}
	},
	onLoad() {
		this.getMenusList()
		console.log('6666');
	},
	methods: {
		// 获取menus
		getMenusList() {
			const that = this
			uni.request({
				url: 'http://localhost:3000/website/getMenusList',
				method: 'GET',
				success(res) {
					const { items } = res.data
					console.log(items);
					let temp = {}
					// 取icons和items的交集
					that.content = items.filter(e => {
						if (that.icons.includes(e.name)) {
							return e
						}
					})
					for (const i in that.icons) {
						for (const j in that.content) {
							if (that.content[j].name === that.icons[i]) {
								temp = {
									id: parseInt(i) + 1,
									icon: that.icons[i],
									content: that.content[j]
								}
								break
							}
						}
						that.menuLists.push(temp)
						temp = {}
					}
					that.menuLists.push({
						id: 15,
						icon: '服务热线',
						content: { name: '服务热线', index: '/jyzn/fwrx' }
					})
					console.log(that.menuLists);
				}
			})
		},
		gotoTargePage(temp) {
			uni.navigateTo({
				url: `content/${temp.index}?category=${temp.category}`
			})
		}
	}
}
	
</script>

